@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.destacados {
    background-color: v.$primario;
    &.temaClaro {
        background-color: v.$fondo
    }
    user-select: none;
    &__panel {
        display: flex;
        flex-direction: column;
        width: 90%;
        margin: 0 auto;
        padding: 6rem 0;
    }
    &__titulo-h2 {
        margin: 0;
        padding: 0;
        padding-bottom: 2rem;
        color: v.$letrasDos;
        &.temaClaro {
            color: v.$letras;
        }
    }
    &__resumen {
        margin: 0;
        padding: 0;
        margin-bottom: 2rem;
        color: darken(v.$secundario, 30);
        &.temaClaro {
            color: darken(v.$letras, 30);
            background-color: v.$fondoOpacity;
        }
        text-align: justify;
    }
    &__grid {
        margin: 0 auto;
        width: 100%;
        display: grid;
        grid-template-rows: repeat(2, 1fr);
        gap: 6rem;
        @include m.tablet {
            grid-template-rows: none;
            grid-template-columns: repeat(2, 1fr);
        }
    }
    &__box {
        transition-property: box shadow;
        transition-duration: 0.4s;
        padding: 0.6rem;
        height: auto;
        border: 2px solid v.$transparente;
        border-radius: 8px;
        &:hover {
            border-color: v.$letrasTitulo;
            cursor: pointer;
        }
    }
    &__h3 {
        padding: 0;
        padding-bottom: 1rem;
        margin: 0;
        margin-top: 2rem;
        font-size: 2.2rem;
        text-align: start;
        color: v.$letrasTitulo;
    }
    &__texto {
        padding: 0;
        margin: 0;
        font-size: 1.6rem;
        text-align: justify;
        color: darken(v.$secundario, 30);
        &.temaClaro {
            color: darken(v.$letras, 30);
        }
    }
    &__imagen {
        width: 100%;
        height: 26rem;
        min-height: 26rem;
        max-height: 26rem;
        @include m.desktop {
            height: 32rem;
            min-height: 32rem;
            max-height: 32rem;
        }
        border-radius: 8px;
    }
}