@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.bienvenida {
    @include m.bgImage( "../img/fondo-bienvenida Dos.webp", v.$primarioOpacity);
    &.temaClaro {
        @include m.bgImage( "../img/fondo-bienvenida Dos.webp", v.$primarioOpacity);
    }
    display: flex;
    position: relative;
    top: 4rem;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 70rem;
    &__principal-box {
        background-color: lighten(v.$primario, 3);
        &.temaClaro {
            background-color: darken(v.$fondo, 3);
        }
        margin-top: 14rem;
        width: 40rem;
        padding: 1rem;
        padding-bottom: 3rem;
        border-radius: 12px 12px 0 0;
        position: relative;
        &.activo {
            margin-top: 0;
            width: 60rem;
            @include m.tablet {
                width: 40%;
            }
        }
    }
    &__fotoperfil-borde {
        margin: -17rem auto 0 auto;
        width: 34rem;
        height: 34rem;
        @include m.tablet {
            width: 34rem;
            height: 34rem;
        }
        border: 2rem solid lighten(v.$negro, 3);
        &.temaClaro {
            border-color: darken(v.$fondo, 20);
        }
        border-radius: 100%;
        transform: scale(1);
        &.activo {
            display: none;
        }
    }
    &__fotoperfil-principal{
        background-color: v.$secundario;
        background-image: url("../img/pablo.webp");
        background-repeat: no-repeat;
        background-size: 100%;
        background-position: center center;
        width: 30rem;
        height: 30rem;
        @include m.tablet {
            width: 30rem;
            height: 30rem;
        }
        border: 2px solid v.$letrasTitulo;
        border-radius: 100%;
        transform: scale(1);
        &.activo {
            display: none;
        }
    }
    &__titulo-bienvenida {
        width: 90%;
        margin: 0 auto;
        padding: 1rem 0;
        &.activo {
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            align-items: center;
            gap: 1rem;
        }
    }
    &__nombre-h2 {
        text-align: center;
        font-size: 2.8rem;
        color: v.$letrasDos;
        &.temaClaro {
            color: v.$letras;
        }
        text-transform: capitalize;
        margin: 0 auto 2rem auto;
        &.activo {
            margin: 0 auto;
        }
    }
    &__ocupacion {
        display: block;
        text-align: center;
        font-size: 1.2rem;
        text-transform: capitalize;
        color: v.$secundario;
        &.temaClaro {
            color: darken(v.$letras, 1) ;
        }
    }
    &__navegacion {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        width: 40%;
        margin: 1rem auto 1rem auto;
        padding: 0.6rem;
        border-radius: 0.5rem;
        background-color: lighten(v.$primario, 1);
        &.temaClaro {
            background-color: lighten(v.$primario, 20);
        }
        transform: scale(1);
    }
    &__link-bienvenida {
        text-decoration: none;
    }
    &__redsocial-box {
        padding: 0.6rem;
        border-radius: 100%;
    }
    &__redsocial-img {
        width: 2rem;
        height: 2rem;
    }
    &__contacto {
        margin: 1rem auto;
        padding: 1rem;
        width: 60%;
        display: block;
        background-color: v.$letrasTitulo;
        color: lighten(v.$primario, 1);
        font-size: 2rem;
        text-transform: uppercase;
        font-weight: 900;
        text-align: center;
        border: 0;
        &:active {
            color: v.$letrasTitulo;
            background-color: lighten(v.$primario, 1);
        }
        &:hover {
            cursor: pointer;
        }
        &.activo {
            width: 60%;
            background-color: red;
        }
    }
    &__formulario {
        display: none;
        &.activo {
            display: block;
            margin: 0 auto;
            width: 90%;
            padding-top: 2rem;
            display: flex;
            flex-direction: column;
            gap: 1rem;
            transform: scale(1);
        }
    }
    &__boxForm {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-around;
        gap: 2rem;
    }
    &__inputForm,
    &__textareaForm {
        background-color: lighten(v.$primario, 10);
        color: v.$secundario;
        &.temaClaro {
            background-color: v.$secundario;
            color: lighten(v.$primario, 10);
        }
        width: 100%;
        height: 3rem;
        border: none;
        padding: .5rem 1rem;
    }
    &__textareaForm {
        height: 20rem;
        min-height: 20rem;
        max-height: 20rem;
        min-width: 100%;
        max-width: 100%;
    }
}