@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.header {
    background-color: lighten(v.$primario, 1);
    &.temaClaro {
        background-color: lighten(v.$fondo, 1);
    }
    position: fixed;
    z-index: 2;
    padding: 0rem;
    margin: 0;
    width: 100%;
    user-select: none;
    & .material-symbols-outlined {
        font-variation-settings:
        'FILL' 1,
        'wght' 900,
        'GRAD' 0,
        'opsz' 48;
    
        color: v.$fondo;
        &.temaClaro {
            color: v.$letras;
        }
    }
    &__grid {
        display: flex;
        gap: 2rem;
        align-items: center;
        justify-content: center;
        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
            padding: 0 4rem;
        }
        #menu {
            position: absolute;
            right: 1.5rem;
            &:hover {
                cursor: pointer;
            }
        }
    }
    &__titulo-h1 {
        color: v.$letrasTitulo;
        font-size: 3rem;
        text-transform: capitalize;
    }
    &__menu {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        position: fixed;
        gap: .2rem;
        padding: 1rem 2rem;
        top: 4.5rem;
        z-index: 2;
        right: -20rem;
        transition: right .6s ease-in-out;
        background-color: lighten(v.$primario, 1);
        border-end-start-radius: 1rem;
        &.temaClaro {
            background-color: v.$fondo;
        }
        &.activo {
            right: 0;
        }

    }
    &__nav {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        gap: 2rem;
        width: 100%;
        border-bottom: 2px solid v.$secundario;
        margin-top: 1rem;
        &:hover {
            cursor: pointer;
        }
        padding-bottom: 1rem;
        &:nth-last-child(1) {
            border-bottom: none;
        }
    }
    &__link {
        text-transform: uppercase;
        color: v.$letrasDos;
        &.temaClaro {
            color: v.$letras;
        }
        font-size: 1.6rem;
        font-weight: 700;
    }
}